import { useForm } from "react-hook-form";
import { useActionData, useTransition, Form } from "remix";
import { useRef } from "react";
import emailjs from "@emailjs/browser";

export default function Contact() {
  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm(
        "YOUR_SERVICE_ID",
        "YOUR_TEMPLATE_ID",
        form.current,
        "YOUR_USER_ID"
      )
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
  };
  const transition = useTransition();

  const actionData = useActionData();

  const {
    register,
    handleSubmit,
    watch,
    formState: { errors },
  } = useForm();
  const onSubmit = (data) => console.log(data);

  console.log(watch("example"));

  return (
    <>
      <div>
        <h1>Contact Me</h1>
        <Form className="form-control" ref={form} onSubmit={sendEmail}>
          {/* <fieldset disabled={transition.state === "submitting"}></fieldset> */}
          <label>
            Your name:{" "}
            <input
              type="text"
              name="user_name"
              defaultValue="name"
              defaultValue={actionData ? actionData.values.name : undefined}
            />
          </label>
          <label>
            Your email:{" "}
            <input
              type="email"
              name="user_email"
              defaultValue="name"
              defaultValue={actionData ? actionData.values.name : undefined}
            />
          </label>
          <button type="SUBMIT" value="Send" className="btn btn-block">
            Send
            {transition.submission ? "Sending..." : "Sent"}
          </button>
        </Form>
      </div>
    </>
  );
}
